@keyframes headerShine {
	100% { left: 150%; }
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes fadeOut {
    from { opacity: 1; }
    to { opacity: 0; }
}

@keyframes zoomIn {
    from { opacity: 0; transform: scale3d(0.3, 0.3, 0.3); }
    to { opacity: 1; }
}

@keyframes zoomOut {
    from { opacity: 1; }
    50% { opacity: 0; transform: scale3d(0.3, 0.3, 0.3); }
    to { opacity: 0; }
}